.professionl {
	.section-container {
		background-color: #fff;
		display: flex;

		flex-direction: column;
		overflow: hidden;
	}

	.header-backdrop {
		background-color: rgba(255, 255, 255, 0.4);
		align-self: center;
		width: 252px;
		height: 57px;
	}

	.main-backdrop {
		background-color: #dfe3ee;
		margin: 22px 0 0;
		width: 100%;
		padding: 41px 9px 181px 35px;
	}

	.content-wrapper {
		gap: 20px;
		display: flex;
	}

	.news-events-column {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 31%;
	}

	.news-events-container {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex-grow: 1;
		flex-direction: column;
		width: 100%;
		padding: 76px 47px;
	}

	.news-blog-header {
		color: #000;
		font: 700 36px Inter, sans-serif;
	}

	.news-content {
		display: flex;
		margin: 40px 0 0;
		padding: 0 0 0 20px;
		flex-direction: column;
	}

	.news-image-wrapper {
		display: flex;
		flex-direction: column;
		border-radius: 0;
		position: relative;
		aspect-ratio: 1.73;
	}

	.news-image {
		position: absolute;
		inset: 0;
		height: 100%;
		width: 100%;
		object-fit: cover;
		object-position: center;
	}

	.news-tag {
		color: #ff9e55;
		margin: 26px 0 0;
		font: 600 24px Inter, sans-serif;
	}

	.news-title {
		color: #000;
		margin: 19px 27px 0 0;
		font: 700 24px Inter, sans-serif;
	}

	.blog-content {
		margin: 125px 0 0 20px;
	}

	.blog-image-wrapper {
		display: flex;
		flex-direction: column;
		border-radius: 0;
		position: relative;
		aspect-ratio: 1.74;
	}

	.blog-tag {
		color: #ff9e55;
		margin: 15px 0 0;
		font: 600 24px Inter, sans-serif;
	}

	.blog-title {
		color: #000;
		margin: 10px 0 0;
		font: 700 24px Inter, sans-serif;
	}

	.see-more-btn {
		border-radius: 10px;
		background-color: #fff;
		align-self: center;
		margin: 147px 0 0;
		width: 166px;
		color: #000;
		font: 700 24px Inter, sans-serif;
		border: 1px solid #000;
		padding: 18px 17px 29px;
	}

	.events-section {
		margin: 91px 0 0;
		font-family: Inter, sans-serif;
	}

	.events-header {
		font-size: 36px;
		font-weight: 700;
	}

	.event-card {
		margin: 47px 0 0;
		padding: 0 0 0 20px;
		font-size: 24px;
	}

	.event-date {
		color: #df3030;
		font-weight: 600;
		margin: 26px 0 0;
	}

	.event-title {
		font-weight: 700;
		margin: 15px 0 0;
	}

	.event-location {
		font-weight: 500;
		margin: 32px 0 0;
	}

	.professionals-column {
		width: 69%;
		margin: 0 0 0 20px;
	}

	.professionals-container {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		padding: 64px 46px 243px;
	}

	.professionals-icon {
		aspect-ratio: 1;
		width: 212px;
		align-self: center;
	}

	.professionals-title {
		align-self: center;
		margin: 44px 0 0;
		font: 700 36px Inter, sans-serif;
	}

	.professionals-description {
		font: 400 22px/33px Inter, sans-serif;
		margin: 52px 0 0;
	}

	.action-button {
		background-color: transparent;
		display: flex;
		gap: 20px;
		justify-content: space-between;
		padding: 25px 41px;
		border: 1px solid #000;
		margin: 45px 0 0;
	}

	.action-icon {
		aspect-ratio: 1;
		width: 50px;
	}

	@media (max-width: 991px) {
		.section-container {
			padding-top: 100px;
		}

		.main-backdrop {
			padding: 0 0 100px 20px;
		}

		.content-wrapper {
			flex-direction: column;
			align-items: stretch;
			gap: 0;
		}

		.news-events-column,
		.professionals-column {
			width: 100%;
			margin: 40px 0 0;
		}

		.news-events-container {
			padding: 0 20px;
		}

		.news-title,
		.event-location {
			margin-right: 10px;
		}

		.blog-content {
			margin: 40px 0 0 10px;
		}

		.see-more-btn {
			margin-top: 40px;
			padding: 0 20px;
		}

		.events-section {
			margin-top: 40px;
		}

		.event-card {
			margin-top: 40px;
		}

		.professionals-container {
			padding: 0 20px 100px;
		}

		.professionals-description {
			margin: 40px 10px 0 0;
		}

		.action-button {
			padding: 0 20px;
		}
	}
}
