.main-contant {
	.page-container {
		background-color: #fff;
		display: flex;

		flex-direction: column;
	}

	.content-backdrop {
		background-color: #dfe3ee;
		width: 100%;
		padding: 41px 51px 192px;
	}

	.content-wrapper {
		gap: 20px;
		display: flex;
	}

	.sidebar {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 39%;
	}

	.info-card {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex-grow: 1;
		flex-direction: column;
		font-family: Inter, sans-serif;
		width: 100%;
		padding: 31px 31px 491px;
	}

	.contact-info {
		display: flex;
		width: 406px;
		max-width: 100%;
		flex-direction: column;
		align-items: start;
		font-size: 24px;
		color: #000;
		font-weight: 400;
	}

	.section-title {
		font-size: 36px;
		font-weight: 700;
	}

	.address {
		align-self: stretch;
		margin-top: 86px;
	}

	.phone {
		margin-top: 63px;
	}

	.email {
		margin-top: 35px;
	}

	.action-buttons {
		align-self: center;
		display: flex;
		margin-top: 102px;
		width: 294px;
		max-width: 100%;
		flex-direction: column;
		font-size: 24px;
		font-weight: 700;
	}

	.primary-btn {
		border-radius: 10px;
		background-color: #0795d7;
		color: #fff;
		margin: 0 27px;
		padding: 19px 29px;
	}

	.secondary-btn {
		border-radius: 10px;
		margin-top: 41px;
		color: #0795d7;
		padding: 19px 29px;
		border: 4px solid #0795d7;
	}

	.news-section {
		display: flex;
		margin-top: 170px;
		flex-direction: column;
	}

	.news-item {
		display: flex;
		margin-top: 66px;
		padding-left: 27px;
		flex-direction: column;
		font-size: 24px;
	}

	.news-image {
		aspect-ratio: 1.77;
		object-fit: contain;
		width: 100%;
		border-radius: 0;
	}

	.category-tag {
		color: #ff9e55;
		font-weight: 600;
		align-self: start;
		margin-top: 35px;
	}

	.news-title {
		color: #000;
		font-weight: 700;
	}

	.see-more-btn {
		border-radius: 10px;
		background-color: #fff;
		align-self: center;
		width: 200px;
		max-width: 100%;
		font-size: 24px;
		color: #000;
		font-weight: 700;
		padding: 25px 47px 44px;
		border: 1px solid #000;
	}

	.main-content {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 61%;
	}

	.social-feed {
		display: flex;
		flex-grow: 1;
		flex-direction: column;
	}

	.post-card {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex-direction: column;
		padding: 22px 0 36px;
		margin-bottom: 48px;
	}

	.post-header {
		display: flex;
		align-items: center;
		padding: 0 34px;
	}

	.profile-image {
		width: 100px;
		height: 100px;
		object-fit: contain;
	}

	.post-meta {
		margin-left: 20px;
	}

	.author-name {
		font-size: 24px;
		font-weight: 700;
		color: #000;
	}

	.post-time {
		font-size: 20px;
		font-weight: 700;
		color: #000;
	}

	.post-content {
		margin: 19px 0;
		padding: 0 34px;
		font-size: 24px;
		color: #000;
		font-weight: 500;
	}

	.post-image {
		width: 100%;
		object-fit: contain;
	}

	.engagement-section {
		display: flex;
		align-items: center;
		padding: 0 34px;
		margin-top: 28px;
	}

	.like-count {
		display: flex;
		align-items: center;
		gap: 11px;
		font-size: 24px;
		font-weight: 500;
	}

	.share-count {
		margin-left: auto;
		font-size: 24px;
		font-weight: 500;
	}

	.action-buttons-container {
		display: flex;
		justify-content: space-between;
		padding: 33px 34px;
		font-size: 24px;
		font-weight: 500;
	}

	.comment-input {
		border-radius: 50px;
		background-color: #d9d9d9;
		width: 764px;
		max-width: 100%;
		margin: 50px auto 0;
		padding: 25px 64px;
		font-size: 24px;
		font-weight: 500;
		border: none;
	}

	.visually-hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}

	@media (max-width: 991px) {
		.page-container {
			padding-top: 100px;
		}

		.content-backdrop {
			padding: 0 20px 100px;
		}

		.content-wrapper {
			flex-direction: column;
			align-items: stretch;
			gap: 0;
		}

		.sidebar,
		.main-content {
			width: 100%;
		}

		.info-card {
			margin-top: 40px;
			padding: 0 20px 100px;
		}

		.action-buttons {
			margin-top: 40px;
		}

		.news-section {
			margin-top: 40px;
		}

		.post-card {
			margin-top: 40px;
		}

		.comment-input {
			margin-top: 40px;
			padding: 0 20px;
		}
	}
}
