footer {
	.footer-container {
		margin-top: 100px;
		display: flex;
		flex-direction: column;
	}

	.footer-content {
		background-color: rgba(51, 144, 206, 1);
		display: flex;
		width: 100%;
		gap: 40px 100px;
		flex-wrap: wrap;
		padding: 25px 80px;
	}

	.social-icons-wrapper {
		display: flex;
		align-items: start;
		gap: 34px;
	}

	.social-icon {
		aspect-ratio: 1;
		object-fit: contain;
		object-position: center;
		width: 40px;
	}

	.copyright-text {
		color: rgba(4, 11, 42, 1);
		letter-spacing: -0.24px;
		text-align: center;
		flex-grow: 1;
		width: 769px;
		margin: auto 0;
		font: 400 16px Montserrat, sans-serif;
	}

	.visually-hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}

	@media (max-width: 991px) {
		.footer-content {
			max-width: 100%;
			padding: 0 20px;
		}

		.copyright-text {
			max-width: 100%;
		}
	}
}
