header {
	.header-container {
		display: flex;
		flex-direction: column;
		align-items: center;
		background-color: rgb(255, 255, 255);
		padding-bottom: 50px;
	}
	.banner-image {
		aspect-ratio: 3.15;
		object-fit: contain;
		object-position: center;
		width: 100%;
		align-self: stretch;
	}
	@media (max-width: 991px) {
		.banner-image {
			max-width: 100%;
		}
	}
	.content-wrapper {
		z-index: 10;
		display: flex;
		margin-top: -48px;
		width: 100%;
		max-width: 1459px;
		gap: 40px 100px;

		align-items: center;
	}
	@media (max-width: 991px) {
		.content-wrapper {
			max-width: 100%;
			justify-content: center;
		}
	}
	.logo-title-container {
		flex-grow: 1;
		flex-basis: auto;
	}
	@media (max-width: 991px) {
		.logo-title-container {
			max-width: 100%;
		}
	}
	.logo-title-wrapper {
		gap: 20px;
		display: flex;
		align-items: center;
	}
	@media (max-width: 991px) {
		.logo-title-wrapper {
			flex-direction: column;
			align-items: stretch;
			gap: 0;
		}
	}
	.logo-column {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 22%;
		margin-left: 0;
	}
	@media (max-width: 991px) {
		.logo-column {
			width: 100%;
		}
	}
	.logo-circle {
		padding: 32px;
	}
	@media (max-width: 991px) {
		.logo-circle {
			padding: 20px;
		}
	}
	.logo-image {
		border-radius: 120px;
	}
	.title-column {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 78%;
		margin-left: 20px;
	}
	@media (max-width: 991px) {
		.title-column {
			width: 100%;
		}
	}
	.title-wrapper {
		display: flex;
		margin-top: 66px;
		flex-direction: column;
		font-family: Inter, sans-serif;
		font-weight: 700;
	}
	@media (max-width: 991px) {
		.title-wrapper {
			max-width: 100%;
			margin-top: 40px;
		}
	}
	.main-title {
		color: rgba(6, 44, 82, 1);
		font-size: 36px;
		align-self: start;
	}
	@media (max-width: 991px) {
		.main-title {
			max-width: 100%;
		}
	}
	.subtitle {
		color: rgba(24, 96, 168, 1);
		font-size: 48px;
		margin-top: 19px;
	}
	@media (max-width: 991px) {
		.subtitle {
			max-width: 100%;
			font-size: 40px;
		}
	}

	.apply-button {
		margin-top: 10px;
		border-radius: 10px;
		text-decoration: underline;
		background-color: rgba(7, 149, 215, 1);
		color: rgba(255, 255, 255, 1);
		font: 700 24px Inter, sans-serif;
		border: none;
		height: 51px;
		padding: 40px 50px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.apply-button:hover {
		background-color: rgb(47, 255, 57);
		transition: 1s;
	}

	@media (max-width: 991px) {
	}
	.divider {
		margin-top: 16px;
		width: 1442px;
		max-width: 100%;
		height: 1px;
		border: 1px solid rgba(0, 0, 0, 1);
	}
	.nav-container {
		display: flex;
		margin-top: 41px;
		width: 100%;
		max-width: 1443px;
		gap: 20px;
		font-family: Inter, sans-serif;
		color: rgba(6, 44, 82, 1);
		font-weight: 500;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	@media (max-width: 991px) {
		.nav-container {
			max-width: 100%;
			margin-top: 40px;
		}
	}
	.primary-nav {
		display: flex;
		gap: 35px;
		font-size: 24px;
	}
	.secondary-nav {
		align-self: start;
		display: flex;
		gap: 38px;
		font-size: 20px;
	}
	.visually-hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}
}
