.event {
	.events-backdrop {
		background-color: rgba(223, 227, 238, 1);
		display: flex;
		padding: 98px 18px 98px 57px;
	}
	.events-container {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex-direction: column;
		flex: 1;
		padding: 56px 43px 174px;
	}
	.logo {
		aspect-ratio: 1;
		object-fit: contain;
		width: 212px;
		align-self: center;
		margin-left: 13px;
	}
	.section-title {
		color: #000;
		align-self: center;
		margin-top: 47px;
		font: 600 36px Inter, sans-serif;
	}
	.events-grid {
		margin-top: 79px;
		width: 100%;
	}
	.events-row {
		gap: 20px;
		display: flex;
	}
	.event-card {
		border-radius: 10px;
		background-color: #fff;
		padding: 22px 33px;
		border: 1px solid #908888;
		width: 50%;
	}
	.event-content {
		gap: 20px;
		display: flex;
	}
	.event-image-wrapper {
		width: 40%;
	}
	.event-image {
		aspect-ratio: 1.29;
		object-fit: contain;
		border-radius: 10px;
		width: 100%;
	}
	.event-details {
		width: 60%;
		display: flex;
		flex-direction: column;
		color: #000;
		font: 24px Inter, sans-serif;
	}
	.event-date {
		color: #df3030;
		font-weight: 600;
	}
	.event-title {
		font-weight: 700;
	}
	.event-location {
		font-weight: 500;
		margin-top: 4px;
	}
	.scroll-indicator {
		aspect-ratio: 0.25;
		object-fit: contain;
		width: 68px;
		align-self: start;
		margin-top: 41px;
	}
	@media (max-width: 991px) {
		.events-backdrop {
			padding: 0 0 100px 20px;
		}
		.events-container {
			max-width: 100%;
			padding: 0 20px 100px;
		}
		.section-title {
			max-width: 100%;
			margin-top: 40px;
		}
		.events-grid {
			max-width: 100%;
			margin-top: 40px;
		}
		.events-row {
			flex-direction: column;
		}
		.event-card {
			width: 100%;
			margin-top: 40px;
			padding: 0 20px;
		}
		.event-content {
			flex-direction: column;
		}
		.event-image-wrapper {
			width: 100%;
		}
		.event-image {
			margin-top: 40px;
		}
		.event-details {
			width: 100%;
			margin-top: 40px;
		}
		.scroll-indicator {
			margin-top: 40px;
		}
	}
}
