.project {
	.student-projects {
		background-color: #fff;
		display: flex;

		flex-direction: column;
		overflow: hidden;
	}

	.highlight-block {
		background-color: rgba(255, 255, 255, 0.4);
		display: flex;
		margin-left: 388px;
		width: 221px;
		height: 46px;
	}

	.projects-container {
		background-color: #dfe3ee;
		display: flex;
		margin-top: 25px;
		align-items: start;
		flex-wrap: wrap;
		padding: 64px 31px 505px 80px;
	}

	.content-card {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex-direction: column;
		align-items: center;
		flex: 1 0 0;
		width: fit-content;
		padding: 118px 80px 262px;
	}

	.logo {
		aspect-ratio: 1;
		object-fit: contain;
		object-position: center;
		width: 200px;
		max-width: 100%;
	}

	.section-title {
		color: #000;
		margin-top: 67px;
		font: 600 36px Inter, sans-serif;
	}

	.section-description {
		color: #000;
		margin-top: 91px;
		font: 700 24px Inter, sans-serif;
	}

	.gallery-wrapper {
		align-self: stretch;
		display: flex;
		margin-top: 44px;
		align-items: center;
		gap: 12px;
		flex-wrap: wrap;
	}

	.gallery-icon {
		aspect-ratio: 1;
		object-fit: contain;
		object-position: center;
		width: 58px;
		align-self: stretch;
		margin: auto 0;
	}

	.gallery-main-image {
		aspect-ratio: 2;
		object-fit: contain;
		object-position: center;
		width: 100%;
		align-self: stretch;
	}

	.project-details {
		color: #000;
		align-self: stretch;
		margin: 87px 99px 0 97px;
		font: 400 24px Inter, sans-serif;
	}

	.project-title {
		font-weight: 700;
	}

	.project-description {
		font-weight: 500;
		margin-top: 1em;
	}

	.side-decoration {
		aspect-ratio: 0.25;
		object-fit: contain;
		object-position: center;
		width: 68px;
		margin-top: 101px;
	}

	@media (max-width: 991px) {
		.student-projects {
			padding-top: 100px;
		}

		.highlight-block {
			margin-left: 10px;
		}

		.projects-container {
			padding: 0 20px 100px;
		}

		.content-card {
			max-width: 100%;
			padding: 100px 20px;
		}

		.section-title {
			margin-top: 40px;
		}

		.section-description {
			max-width: 100%;
			margin-top: 40px;
		}

		.gallery-wrapper {
			max-width: 100%;
			margin-top: 40px;
		}

		.gallery-main-image {
			max-width: 100%;
		}

		.project-details {
			max-width: 100%;
			margin: 40px 10px 0 0;
		}

		.side-decoration {
			margin-top: 40px;
		}
	}
}
