.programs {
	.programs-container {
		background: #fff;
		display: flex;

		flex-direction: column;
		overflow: hidden;
	}

	.header-block {
		background: rgba(255, 255, 255, 0.4);
		display: flex;
		margin-left: 224px;
		width: 148px;
		height: 49px;
	}

	.content-backdrop {
		background: #dfe3ee;
		display: flex;
		margin-top: 28px;
		align-items: start;
		padding: 58px 18px 208px 63px;
	}

	.content-wrapper {
		border-radius: 10px;
		background: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		padding: 0 0 125px;
		flex-direction: column;
		flex: 1;
		width: fit-content;
	}

	.header-image {
		aspect-ratio: 1;
		object-fit: contain;
		width: 212px;
		align-self: center;
		z-index: 10;
		margin-top: -14px;
	}

	.courses-grid {
		display: flex;
		margin-top: 23px;
		width: 100%;
		flex-direction: column;
		padding: 0 43px;
	}

	.course-row {
		display: flex;
		gap: 20px;
		margin-top: 50px;
		width: 100%;
	}

	.course-card {
		border-radius: 10px;
		background: #fff;
		flex: 1;
		padding: 30px;
		border: 1px solid #908888;
	}

	.course-content {
		display: flex;
		gap: 20px;
	}

	.course-icon {
		border-radius: 10px;
		background: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex: 1;
		justify-content: center;
		padding: 21px 44px;
	}

	.course-image {
		aspect-ratio: 1;
		object-fit: contain;
		width: 119px;
	}

	.course-title {
		color: #000;
		font: 700 24px Inter, sans-serif;
		margin: auto 0;
	}

	.coming-soon-label {
		color: #df3030;
		font: 500 24px Inter, sans-serif;
	}

	.footer-image {
		aspect-ratio: 0.25;
		object-fit: contain;
		width: 68px;
		margin-top: 107px;
	}

	@media (max-width: 991px) {
		.programs-container {
			padding-top: 100px;
		}

		.header-block {
			margin-left: 10px;
		}

		.content-backdrop {
			padding: 0 0 100px 20px;
		}

		.content-wrapper {
			padding-bottom: 100px;
		}

		.courses-grid {
			padding: 0 20px;
		}

		.course-row {
			flex-direction: column;
			margin-top: 40px;
		}

		.course-card {
			padding: 20px;
		}

		.course-content {
			flex-direction: column;
		}

		.course-icon {
			padding: 20px;
		}

		.footer-image {
			margin-top: 40px;
		}
	}
}
