.students {
	.visually-hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}
	.page-section {
		background-color: rgba(223, 227, 238, 1);
		margin-top: 35px;
		width: 100%;
		padding: 41px 39px 165px;
	}
	.content-wrapper {
		gap: 20px;
		display: flex;
	}
	.news-column {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 32%;
	}
	.news-container {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex-grow: 1;
		flex-direction: column;
		width: 100%;
		padding: 76px 47px;
	}
	.news-header {
		display: flex;
		flex-direction: column;
	}
	.section-title {
		color: #000;
		font: 700 36px Inter, sans-serif;
	}
	.news-item {
		display: flex;
		margin-top: 40px;
		padding-left: 20px;
		flex-direction: column;
	}
	.image-wrapper {
		display: flex;
		flex-direction: column;
		border-radius: 0;
		position: relative;
		aspect-ratio: 1.73;
	}
	.news-image {
		position: absolute;
		inset: 0;
		height: 100%;
		width: 100%;
		object-fit: cover;
		object-position: center;
	}
	.category-label {
		color: rgba(255, 158, 85, 1);
		margin-top: 26px;
		font: 600 24px Inter, sans-serif;
	}
	.news-title {
		color: #000;
		margin: 19px 27px 0 0;
		font: 700 24px Inter, sans-serif;
	}
	.blog-item {
		align-self: start;
		display: flex;
		flex-direction: column;
		align-items: start;
		margin: 125px 0 0 20px;
	}
	.action-button {
		border-radius: 10px;
		background-color: #fff;
		align-self: center;
		display: flex;
		margin-top: 147px;
		width: 166px;
		max-width: 100%;
		flex-direction: column;
		color: #000;
		font: 700 24px Inter, sans-serif;
		border: 1px solid #000;
		padding: 18px 17px 29px;
	}
	.events-section {
		display: flex;
		margin-top: 91px;
		flex-direction: column;
		font-family: Inter, sans-serif;
		color: #000;
	}
	.event-item {
		display: flex;
		margin-top: 47px;
		padding-left: 20px;
		flex-direction: column;
		font-size: 24px;
	}
	.event-date {
		color: rgba(223, 48, 48, 1);
		font-weight: 600;
		margin-top: 26px;
	}
	.event-title {
		font-weight: 700;
		margin-top: 15px;
	}
	.event-location {
		font-weight: 500;
		margin-top: 32px;
	}
	.students-column {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 68%;
		margin-left: 20px;
	}
	.students-container {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		width: 100%;
		flex-direction: column;
		font-family: Inter, sans-serif;
		font-weight: 700;
		margin: 0 auto;
		padding: 44px 0 110px;
	}
	.header-content {
		align-self: end;
		display: flex;
		margin-right: -31px;
		width: 656px;
		max-width: 100%;
		align-items: flex-start;
		gap: 20px;
		font-size: 36px;
		color: #000;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.main-content {
		display: flex;
		margin-top: 44px;
		width: 100%;
		flex-direction: column;
		padding: 0 54px;
	}
	.highlight-text {
		color: rgba(255, 158, 85, 1);
	}
	.description {
		color: #000;
		font-size: 22px;
		font-weight: 400;
		line-height: 33px;
		margin: 35px 12px 0 10px;
	}
	.button-container {
		align-self: center;
		display: flex;
		margin-top: 135px;
		width: 650px;
		max-width: 100%;
		gap: 20px;
		font-size: 24px;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.primary-button {
		border-radius: 10px;
		background-color: rgba(7, 149, 215, 1);
		padding: 19px 57px;
		color: #fff;
		border: none;
		cursor: pointer;
	}
	.secondary-button {
		border-radius: 10px;
		padding: 19px 57px;
		border: 4px solid rgba(7, 149, 215, 1);
		color: rgba(7, 149, 215, 1);
		background: transparent;
		cursor: pointer;
	}
	@media (max-width: 991px) {
		.page-section {
			max-width: 100%;
			padding: 0 20px 100px;
		}
		.content-wrapper {
			flex-direction: column;
			align-items: stretch;
			gap: 0;
		}
		.news-column,
		.students-column {
			width: 100%;
		}
		.news-container {
			max-width: 100%;
			margin-top: 40px;
			padding: 0 20px;
		}
		.news-title {
			margin-right: 10px;
		}
		.blog-item {
			margin: 40px 0 0 10px;
		}
		.action-button {
			margin-top: 40px;
			padding: 0 20px;
		}
		.events-section {
			margin-top: 40px;
		}
		.event-item {
			margin-top: 40px;
		}
		.students-container {
			max-width: 100%;
			margin-top: 40px;
			padding-bottom: 100px;
		}
		.description {
			max-width: 100%;
			margin-right: 10px;
		}
		.button-container {
			margin-top: 40px;
		}
		.primary-button,
		.secondary-button {
			padding: 0 20px;
		}
	}
}
