.about {
	.about-section {
		display: flex;

		flex-direction: column;
		overflow: hidden;
	}

	.content-backdrop {
		background-color: #dfe3ee;
		margin-top: 28px;
		width: 100%;
		padding: 41px 36px 0;
	}
	.content-wrapper {
		gap: 20px;
		display: flex;
	}
	.news-column {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 32%;
		margin-left: 0;
	}
	.news-block {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		flex-direction: column;
		width: 100%;
		padding: 76px 47px;
	}
	.news-header {
		display: flex;
		flex-direction: column;
	}
	.section-title {
		color: #000;
		align-self: start;
		font: 700 36px Inter, sans-serif;
	}
	.news-content {
		display: flex;
		margin-top: 40px;
		padding-left: 20px;
		flex-direction: column;
	}
	.image-container {
		display: flex;
		flex-direction: column;
		border-radius: 0;
		position: relative;
		aspect-ratio: 1.73;
	}
	.news-image {
		position: absolute;
		inset: 0;
		height: 100%;
		width: 100%;
		object-fit: cover;
	}
	.news-overlay {
		aspect-ratio: 1.73;
		object-fit: contain;
		width: 100%;
		border-radius: 0;
	}
	.news-tag {
		color: #ff9e55;
		align-self: start;
		margin-top: 26px;
		font: 600 24px Inter, sans-serif;
	}
	.news-title {
		color: #000;
		margin: 19px 27px 0 0;
		font: 700 24px Inter, sans-serif;
	}
	.blog-content {
		align-self: start;
		display: flex;
		flex-direction: column;
		align-items: start;
		margin: 125px 0 0 20px;
	}
	.blog-image-wrapper {
		display: flex;
		flex-direction: column;
		border-radius: 0;
		align-self: stretch;
		position: relative;
		aspect-ratio: 1.74;
		width: 100%;
	}
	.blog-image {
		aspect-ratio: 1.74;
		object-fit: contain;
		width: 100%;
		border-radius: 0;
	}
	.blog-tag {
		color: #ff9e55;
		margin-top: 15px;
		font: 600 24px Inter, sans-serif;
	}
	.blog-title {
		color: #000;
		margin-top: 10px;
		font: 700 24px Inter, sans-serif;
	}
	.action-button {
		border-radius: 10px;
		background-color: #fff;
		align-self: center;
		display: flex;
		margin-top: 147px;
		width: 166px;
		max-width: 100%;
		flex-direction: column;
		color: #000;
		font: 700 24px Inter, sans-serif;
		border: 1px solid #000;
	}
	.button-text {
		border-radius: 10px;
		background-color: #fff;
		padding: 18px 17px 29px;
		border: 1px solid #000;
	}
	.events-section {
		display: flex;
		margin-top: 91px;
		flex-direction: column;
		font-family: Inter, sans-serif;
		color: #000;
	}
	.events-title {
		font-size: 36px;
		font-weight: 700;
		align-self: start;
	}
	.event-card {
		display: flex;
		margin-top: 47px;
		padding-left: 20px;
		flex-direction: column;
		font-size: 24px;
	}
	.event-image {
		aspect-ratio: 1.73;
		object-fit: contain;
		width: 100%;
		border-radius: 0;
	}
	.event-date {
		color: #df3030;
		font-weight: 600;
		align-self: start;
		margin-top: 26px;
	}
	.event-title {
		font-weight: 700;
		align-self: start;
		margin-top: 15px;
	}
	.event-location {
		font-weight: 500;
		margin-top: 32px;
	}
	.about-column {
		display: flex;
		flex-direction: column;
		line-height: normal;
		width: 68%;
		margin-left: 20px;
	}
	.about-block {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		display: flex;
		width: 100%;
		flex-direction: column;
		color: #000;
		margin: 5px auto 0;
		padding: 24px 0 49px 51px;
		font: 700 22px Inter, sans-serif;
	}
	.about-title {
		font-size: 36px;
		align-self: start;
	}
	.mission-wrapper {
		display: flex;
		gap: 26px;
		font-weight: 400;
		line-height: 33px;
		flex-wrap: wrap;
		margin: 23px -19px 0 0;
	}
	.mission-content {
		display: flex;
		flex-direction: column;
		flex-grow: 1;
		flex-basis: 0;
		width: fit-content;
	}
	.mission-text {
		margin-right: 20px;
	}
	.mission-image {
		aspect-ratio: 1.86;
		object-fit: contain;
		width: 773px;
		align-self: end;
		margin-top: 65px;
		max-width: 100%;
	}
	.decorative-image {
		aspect-ratio: 0.25;
		object-fit: contain;
		width: 68px;
		align-self: start;
		margin-top: 55px;
	}
	.program-description {
		font-weight: 400;
		line-height: 33px;
		margin: 70px 82px 0 0;
	}
	.program-image {
		aspect-ratio: 1.55;
		object-fit: contain;
		width: 776px;
		align-self: center;
		margin-top: 46px;
		max-width: 100%;
	}
	.benefits-text {
		font-weight: 400;
		line-height: 33px;
		margin: 57px 71px 0 0;
	}
	.testimonials-title {
		font-size: 28px;
		line-height: 42px;
		align-self: start;
		margin-top: 55px;
	}
	.testimonial-image {
		aspect-ratio: 2.18;
		object-fit: contain;
		width: 824px;
		margin-top: 50px;
		max-width: 100%;
	}
	.testimonial-text {
		font-size: 28px;
		line-height: 42px;
		margin: 38px 90px 0 0;
	}

	@media (max-width: 991px) {
		.about-section {
			padding-top: 100px;
		}
		.header-block {
			margin-left: 10px;
		}
		.content-backdrop {
			max-width: 100%;
			padding: 0 20px;
		}
		.content-wrapper {
			flex-direction: column;
			align-items: stretch;
			gap: 0;
		}
		.news-column {
			width: 100%;
		}
		.news-block {
			max-width: 100%;
			margin-top: 40px;
			padding: 0 20px;
		}
		.news-title {
			margin-right: 10px;
		}
		.blog-content {
			margin: 40px 0 0 10px;
		}
		.action-button {
			margin-top: 40px;
		}
		.button-text {
			padding: 0 20px;
		}
		.events-section {
			margin-top: 40px;
		}
		.event-card {
			margin-top: 40px;
		}
		.event-location {
			margin-right: 6px;
		}
		.about-column {
			width: 100%;
		}
		.about-block {
			max-width: 100%;
			margin-top: 40px;
		}
		.mission-text {
			max-width: 100%;
			margin-right: 10px;
		}
		.mission-image {
			margin-top: 40px;
		}
		.decorative-image {
			margin-top: 40px;
		}
		.program-description {
			max-width: 100%;
			margin: 40px 10px 0 0;
		}
		.program-image {
			margin-top: 40px;
		}
		.benefits-text {
			max-width: 100%;
			margin: 40px 10px 0 0;
		}
		.testimonials-title {
			max-width: 100%;
			margin-top: 40px;
		}
		.testimonial-image {
			margin-top: 40px;
		}
		.testimonial-text {
			max-width: 100%;
			margin-right: 10px;
		}
	}
}
