.contacts {
	.section-backdrop {
		background-color: rgba(223, 227, 238, 1);
		margin-top: 36px;
		width: 100%;
		padding: 41px 27px 184px;
	}

	.content-wrapper {
		display: flex;
		gap: 20px;
	}

	.news-events-column {
		width: 31%;
	}

	.contact-column {
		width: 69%;
		margin-left: 20px;
	}

	.content-card {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		padding: 76px 47px;
	}

	.section-title {
		color: #000;
		font: 700 36px Inter, sans-serif;
	}

	.news-item {
		margin: 40px 0 0 20px;
	}

	.news-image {
		width: 100%;
		border-radius: 0;
		aspect-ratio: 1.73;
		object-fit: cover;
	}

	.news-tag {
		color: #ff9e55;
		font: 600 24px Inter, sans-serif;
		margin-top: 26px;
	}

	.news-headline {
		color: #000;
		font: 700 24px Inter, sans-serif;
		margin: 19px 27px 0 0;
	}

	.blog-item {
		margin: 125px 0 0 20px;
	}

	.blog-image {
		width: 100%;
		border-radius: 0;
		aspect-ratio: 1.74;
		object-fit: cover;
	}

	.action-button {
		border-radius: 10px;
		background-color: #fff;
		border: 1px solid #000;
		color: #000;
		font: 700 24px Inter, sans-serif;
		padding: 18px 30px;
		text-align: center;
		margin: 81px auto 0;
		width: 166px;
		cursor: pointer;
	}

	.events-section {
		margin-top: 91px;
	}

	.event-item {
		margin: 47px 0 0 20px;
	}

	.event-date {
		color: #df3030;
		font: 600 24px Inter, sans-serif;
	}

	.event-title {
		color: #000;
		font: 700 24px Inter, sans-serif;
		margin-top: 15px;
	}

	.event-location {
		color: #000;
		font: 500 24px Inter, sans-serif;
		margin-top: 32px;
	}

	.contact-header {
		display: flex;
		align-items: center;
		margin-bottom: 36px;
	}

	.contact-icon {
		width: 212px;
		aspect-ratio: 1;
		object-fit: contain;
	}

	.contact-title {
		font: 600 36px Inter, sans-serif;
		margin-top: 32px;
	}

	.contact-text {
		font: 400 22px/33px Inter, sans-serif;
		margin-top: 47px;
	}

	.contact-cards {
		display: flex;
		gap: 20px;
		margin-top: 36px;
	}

	.contact-card {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		padding: 27px 32px;
		display: flex;
		align-items: start;
	}

	.contact-info {
		flex: 1;
	}

	.contact-name {
		font: 600 22px Inter, sans-serif;
	}

	.contact-role {
		font: 400 22px/33px Inter, sans-serif;
	}

	.contact-details {
		font: 400 20px/30px Inter, sans-serif;
		margin-top: 26px;
	}

	.contact-photo {
		width: 125px;
		border-radius: 10px;
		aspect-ratio: 0.83;
		object-fit: contain;
	}

	.department-card {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		padding: 33px 29px 54px;
		margin-top: 36px;
	}

	.department-title {
		font: 600 22px Inter, sans-serif;
	}

	.department-address {
		font: 400 22px Inter, sans-serif;
		margin-top: 38px;
	}

	.department-contact {
		font: 400 20px/30px Inter, sans-serif;
		margin-top: 35px;
	}

	.contact-form {
		border-radius: 10px;
		background-color: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		padding: 83px 50px;
		margin-top: 37px;
	}

	.form-title {
		font: 600 36px Inter, sans-serif;
		text-align: center;
	}

	.form-field {
		border-radius: 10px;
		border: 2px solid #2b97f0;
		padding: 16px 35px;
		margin-top: 15px;
		width: 100%;
		font: 400 36px Inter, sans-serif;
	}

	.form-message {
		padding: 27px 35px 189px;
	}

	.submit-button {
		background-color: #2b97f0;
		color: #fff;
		border: 3px solid #2b97f0;
		border-radius: 10px;
		font: 700 24px Inter, sans-serif;
		padding: 24px 69px;
		margin: 57px auto 0;
		width: 221px;
		cursor: pointer;
	}

	.visually-hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}

	@media (max-width: 991px) {
		.section-backdrop {
			padding: 0 0 100px 20px;
		}

		.content-wrapper {
			flex-direction: column;
			gap: 0;
		}

		.news-events-column,
		.contact-column {
			width: 100%;
			margin-left: 0;
		}

		.content-card {
			margin-top: 40px;
			padding: 0 20px;
		}

		.news-item,
		.blog-item {
			margin: 40px 0 0 10px;
		}

		.action-button {
			margin-top: 40px;
			padding: 0 20px;
		}

		.events-section {
			margin-top: 40px;
		}

		.event-item {
			margin: 40px 0 0 10px;
		}

		.contact-header {
			margin-top: 40px;
		}

		.contact-text {
			margin-top: 40px;
		}

		.contact-cards {
			flex-direction: column;
			margin-top: 35px;
		}

		.contact-card {
			margin-top: 33px;
			padding: 0 20px;
		}

		.department-card {
			margin-top: 33px;
			padding: 0 20px;
		}

		.contact-form {
			padding: 0 20px;
		}

		.form-field {
			padding: 0 20px;
		}

		.form-message {
			padding: 0 20px 110px;
		}

		.submit-button {
			margin-top: 40px;
			padding: 0 20px;
		}
	}
}
