.news {
	.news-blog {
		background: #fff;
		display: flex;

		flex-direction: column;
		overflow: hidden;
	}

	.transparent-block {
		background: rgba(255, 255, 255, 0.4);
		align-self: flex-end;
		margin-right: 242px;
		width: 148px;
		height: 49px;
	}

	.content-wrapper {
		background: #dfe3ee;
		margin-top: 19px;
		width: 100%;
		padding: 41px 16px 190px 35px;
	}

	.two-column-layout {
		display: flex;
		gap: 20px;
	}

	.left-column {
		width: 31%;
	}

	.right-column {
		width: 69%;
		margin-left: 20px;
	}

	.card {
		border-radius: 10px;
		background: #fff;
		box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
		padding: 45px 28px;
	}

	.explore-btn {
		border-radius: 10px;
		width: 294px;
		font: 700 24px Inter, sans-serif;
		color: #0795d7;
		padding: 19px 51px;
		border: 4px solid #0795d7;
		text-align: center;
	}

	.location-info {
		border-radius: 10px;
		background: #fff;
		margin-top: 298px;
		padding-bottom: 12px;
		font-size: 16px;
	}

	.address-primary {
		background: #fff;
		z-index: 10;
		margin-top: -20px;
		font-weight: 600;
		padding: 14px 13px;
	}

	.address-secondary {
		font-weight: 400;
		margin: -4px 0 0 13px;
	}

	.news-section {
		margin-top: 34px;
	}

	.section-title {
		font: 700 38px Inter, sans-serif;
		text-align: center;
		margin: 26px 0;
	}

	.post {
		margin-top: 56px;
	}

	.post-header {
		display: flex;
		gap: 13px;
	}

	.profile-img {
		width: 90px;
		height: 90px;
		object-fit: contain;
	}

	.post-meta {
		flex: 1;
	}

	.author {
		font: 700 24px Inter, sans-serif;
	}

	.timestamp {
		font: 700 20px Inter, sans-serif;
		margin-top: 6px;
	}

	.post-content {
		font: 700 24px Inter, sans-serif;
		margin: 36px 17px 0 11px;
	}

	.post-image {
		width: 100%;
		margin: 35px 11px 0;
		aspect-ratio: 1.44;
		object-fit: contain;
	}

	.contact-info {
		font: 700 24px Inter, sans-serif;
		line-height: 36px;
		margin: 33px 20px 0 13px;
	}

	.visually-hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}

	@media (max-width: 991px) {
		.news-blog {
			padding-top: 100px;
		}

		.transparent-block {
			margin-right: 10px;
		}

		.content-wrapper {
			padding: 0 0 100px 20px;
		}

		.two-column-layout {
			flex-direction: column;
		}

		.left-column,
		.right-column {
			width: 100%;
			margin-left: 0;
		}

		.post-content,
		.contact-info {
			margin: 40px 10px 0;
		}

		.post-image {
			margin: 40px 10px 0;
		}
	}
}
